import { css } from '@emotion/react';
import { useCallback, useEffect, useState } from 'react';
import { Link } from 'react-scroll';

import { Typography } from 'src/components/dataDisplay';
import { PlusIcon } from 'src/components/icons';
import { Stack } from 'src/components/layout';
import type { LinkableContent } from 'src/hooks/usePaperItems';

const contentWrapper = (top: number) => css`
  position: absolute;
  top: ${top}px;
`;

const leftStyle = (left: number) => css`
  left: ${left}px;
`;

const rightStyle = (right: number) => css`
  right: ${right}px;
`;

const BASE_WIDTH = 375;

interface LinkerProps extends LinkableContent {}

interface LinkerContentProps
  extends Pick<LinkerProps, 'title' | 'description'> {}
const LinkerContent = ({
  title,
  description,
}: LinkerContentProps): JSX.Element => {
  return (
    <Stack>
      <Typography variant={title.variant}>{title.content}</Typography>
      {description && (
        <Typography variant={description.variant}>
          {description.content}
        </Typography>
      )}
    </Stack>
  );
};

const PointingContent = ({
  title,
  description,
  position,
  direction,
  link,
}: LinkerProps): JSX.Element => {
  const [diff, setDiff] = useState(0);

  const onResize = useCallback(() => {
    const widthDiff = window.innerWidth - BASE_WIDTH;
    const diffForAlign = widthDiff === 0 ? 0 : widthDiff / 2;
    if (direction === 'right') {
      const leftWithDiff = position.left + diffForAlign;
      setDiff(leftWithDiff);
    } else {
      const right = BASE_WIDTH - position.left - 24;
      const rightWithDiff = right + diffForAlign;
      setDiff(rightWithDiff);
    }
  }, [setDiff, direction, position.left]);
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  return (
    <Link to={link} duration={1000} delay={200}>
      <Stack
        direction="row"
        alignItems="center"
        spacing={8}
        css={[
          contentWrapper(position.top),
          direction === 'right' && leftStyle(diff),
          direction === 'left' && rightStyle(diff),
        ]}
      >
        {direction === 'right' && <PlusIcon />}
        <LinkerContent title={title} description={description} />
        {direction === 'left' && <PlusIcon />}
      </Stack>
    </Link>
  );
};

export default PointingContent;
