import { css } from '@emotion/react';

import { PAPER_HEIGHT } from 'src/constants/styles';

interface PaperProps {
  children: React.ReactNode;
  backgroundUrl?: string;
}

const wrapper = css`
  font-family: 'Pretendard Variable';
  height: ${PAPER_HEIGHT}px;
  position: relative;
`;

// background: center / cover no-repeat url(${url});
// width 보다 커지면 빈 부분 나온다...

const background = (url: PaperProps['backgroundUrl']) => css`
  ${url
    ? ` background-image:  url(${url});
    background-repeat: no-repeat;
    background-size: auto 100% ; 
    background-position: center;`
    : `background: #ffffff;`}
`;

const Paper = ({
  children,
  backgroundUrl,
  ...others
}: PaperProps): JSX.Element => {
  return (
    <div css={[wrapper, background(backgroundUrl)]} {...others}>
      {children}
    </div>
  );
};

export default Paper;
