import { useRouter } from 'next/router';

const CARDS = 'cards';
const showCaseTypes = [CARDS] as const;
type ShowCaseType = (typeof showCaseTypes)[number];

const getShowCaseType = (type: string = CARDS): ShowCaseType => {
  return showCaseTypes.includes(type as ShowCaseType)
    ? (type as ShowCaseType)
    : CARDS;
};

const useShowCaseType = (): ShowCaseType => {
  const router = useRouter();
  return getShowCaseType(router.query?.type as string);
};

export default useShowCaseType;
