import { css } from '@emotion/react';

import { PAPER_HEIGHT } from 'src/constants/styles';
import type { PaperType } from 'src/hooks/usePaperItems';
import {
  CENTER,
  CENTER_BOTTOM,
  LEFT_BOTTOM,
  LEFT_TOP,
  RIGHT_BOTTOM,
} from 'src/hooks/usePaperItems';

const MARGIN_Y = 40;

const leftBottom = {
  contentContainer: css`
    height: ${PAPER_HEIGHT - MARGIN_Y}px;
    margin-left: 20px;
    margin-bottom: ${MARGIN_Y}px;
    justify-content: flex-end;
  `,
  titleStyle: css`
    display: block;
  `,
  descriptionStyle: css`
    display: block;
  `,
};

const leftTop = {
  contentContainer: css`
    height: ${PAPER_HEIGHT - MARGIN_Y}px;
    margin-left: 20px;
    margin-top: ${MARGIN_Y}px;
    justify-content: flex-start;
  `,

  titleStyle: css`
    display: block;
  `,

  descriptionStyle: css`
    display: block;
  `,
};

const center = {
  contentContainer: css`
    height: ${PAPER_HEIGHT}px;
    flex: auto;
    justify-content: center;
    align-items: center;
  `,

  titleStyle: css`
    display: flex;
    justify-content: center;
  `,

  descriptionStyle: css`
    display: flex;
    justify-content: center;
  `,
};

const rightBottom = {
  contentContainer: css`
    height: ${PAPER_HEIGHT - MARGIN_Y}px;
    margin-right: 20px;
    margin-bottom: ${MARGIN_Y}px;
    flex: auto;
    justify-content: end;
    align-items: flex-end;
  `,
  titleStyle: css``,

  descriptionStyle: css`
    display: flex;
    justify-content: flex-end;
  `,
};

const centerBottom = {
  contentContainer: css`
    height: ${PAPER_HEIGHT - MARGIN_Y}px;
    margin-bottom: ${MARGIN_Y}px;
    flex: auto;
    justify-content: end;
    align-items: center;
  `,

  titleStyle: css`
    display: flex;
    justify-content: center;
  `,

  descriptionStyle: css`
    display: flex;
    justify-content: center;
  `,
};

export const getContainerStyle = (type: PaperType) => {
  switch (type) {
    case LEFT_BOTTOM:
      return leftBottom;
    case CENTER_BOTTOM:
      return centerBottom;
    case RIGHT_BOTTOM:
      return rightBottom;
    case CENTER:
      return center;
    case LEFT_TOP:
    default:
      return leftTop;
  }
};
