import { css } from '@emotion/react';
import { useEffect, useRef, useState } from 'react';
import { Element, scroller } from 'react-scroll';

import { Stack } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import type { NormalPaperItem } from 'src/hooks/usePaperItems';

import Badge from './Badge';
import PointingContent from './PointingContent';
import { getContainerStyle } from './styles';
import Typographies from './Typographies';

const paperLayout = css`
  display: flex;
  color: #ffffff;
`;

interface NormalPaperProps extends NormalPaperItem {}

const NormalPaper = ({
  imageUrl,
  paperType,
  staticContent,
  linkableContents,
}: NormalPaperProps): JSX.Element => {
  const [isIntersecting, setIsIntersecting] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    // console.log('NormalPaper - useEffect', staticContent.title.content);
    const observer = new IntersectionObserver(
      ([entry]) => {
        // console.log(
        //   'NormalPaper observer',
        //   staticContent.title.content,
        //   entry.isIntersecting,
        // );
        setIsIntersecting(entry.isIntersecting);
      },
      { threshold: 0.3 },
    );
    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => observer.disconnect();
  }, [isIntersecting, ref]);

  useEffect(() => {
    if (isIntersecting) {
      scroller.scrollTo(staticContent.title.content, {
        duration: 5000,
        delay: 200,
      });
    }
  }, [isIntersecting, staticContent.title.content, ref]);

  const { contentContainer, titleStyle, descriptionStyle } =
    getContainerStyle(paperType);

  return (
    <Element name={staticContent.title.content}>
      <Paper backgroundUrl={imageUrl} css={paperLayout}>
        <Stack css={[contentContainer]} spacing={12}>
          {staticContent?.badge && (
            <Badge
              variant={staticContent.badge.variant}
              hasBorder={staticContent.badge.hasBorder}
            >
              {staticContent.badge.content}
            </Badge>
          )}
          <Typographies
            name="title"
            contentAndVariant={staticContent.title}
            isUpperCase
            css={titleStyle}
          />
          <Typographies
            name="description"
            contentAndVariant={staticContent?.description}
            css={descriptionStyle}
          />
        </Stack>
        {linkableContents?.map((content, index) => {
          const key = `linker-${index}`;
          return <PointingContent key={key} {...content} />;
        })}
      </Paper>
    </Element>
  );
};

export default NormalPaper;
